
:root {
  --dark_yellow:#FEDD13;
  --light_yellow:#FDEE17;
  --dark_blue:#282560;
  --medium_blue:#1D4E9E;
  --light_blue:#0477BE;
  --cnpr_red:#E72E2E;
  --cnpr_white:#F1F2F2;
  --cnpr_black:#282827;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.logo_cnpr{
  width:7rem;
  margin: 2rem 1rem; 
  height: auto;
  position: absolute;
  left:0;
}
.main {
  background-color: var(--cnpr_white);
  color: var(--cnpr_black);
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
}
.main_left {
  width: 60vw;
  height: 100%;
  background-color: var(--dark_blue);
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
.main_right {
  width: 40vw;
  height: 100vh;
  background-color: var(--medium_blue);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

}
.main_title {
  color: var(--dark_yellow);
  font-size: 2.8rem;
  margin: 1.5rem 0 2rem 0;
  padding: 0;
  
}
.main_pdf_viewer{
  margin: 2rem 0;
  margin-top: 1rem;
  width: 22em;
  height: 34em;
  border: 1px solid var(--cnpr_black);
  background-color: var(--cnpr_white);
}
.main_pdf_download_button{
  width: 10rem;
  height: 3rem!important;
  background-color: var(--light_yellow);
  color: var(--dark_blue);
  font-weight: bold;
  font-size: 1rem;

  border-radius: 1rem;
  text-align: center;
  -webkit-box-shadow: 7px 10px 33px -18px rgba(0,0,0,0.75);
  -moz-box-shadow: 7px 10px 33px -18px rgba(0,0,0,0.75);
  box-shadow: 7px 10px 33px -18px rgba(0,0,0,0.75);
}
.main_pdf_download_button_text{
  text-align: center;
  margin: 0 auto;
  margin-top: 0.2rem;
  padding: 0;
}
.main_pdf_download_button:hover{
  cursor:pointer;
  background-color: var(--dark_yellow);
}
.dropdown {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap:0.5rem;
  color: gray;
}
.dropdown_title{
  color: var(--cnpr_white);
  font-size: 1.1rem;
}
.dropdown_select{
  width:14rem;
  height: 3rem;
  text-align: center;
  border-radius: 5px;
  color: gray;
}
.main_left_inputs{
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.main_left_inputs_left{
  width: 30vw;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.main_left_inputs_right{
  width: 30vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}
/*text_input, text_input_label, text_input_are*/
.text_input{
  display: flex;
  align-items: center;
  justify-content: center;
  gap:0.5rem;
}
.text_input_label{
  color: var(--cnpr_white);
  font-size: 1.1rem;
  width: 10rem;
}
.text_input_area{ 
  width:12rem;
  height: 2rem;
  text-align: center;
  border-radius: 5px;}
  /* Small dropdown */
  .dropdown_small {
    display: flex;
    align-items: center;
    justify-content: center;
    gap:0.5rem;
  }
  .dropdown_small_title{
    color: var(--cnpr_white);
    font-size: 1.1rem;
    width: 10rem;
  }
  .dropdown_small_select{
    width:12rem;
    height: 2rem;
    text-align: center;
    border-radius: 3px;
  }
  /* text area box */
  .textarea_box{
    margin-top: 1rem;
  width: 22rem;
  height: 10rem;
  border-radius: 5px;
  resize:none}
  /* <div className="checkbox_menu">
      <label className="checkbox_menu_title">{label}</label>
      <div className="checkbox_menu_options">
        {options.map((option) => (
          <label key={option.value} className="checkbox_option"> */
  .checkbox_menu {
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    gap:0.5rem;
  }
  .checkbox_menu_title{
    color: var(--cnpr_white);
    font-size: 1.1rem;
    width: 10rem;
  }
  .main_left_cerinte {
    display: flex;
    gap: 1rem;
    width: 30rem;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center; 
    margin-bottom: 1rem;  
  }
.main_left_cerinte .dropdown_small .dropdown_small_title {
  display: none;
}
.main_left_cerinte_title {
  color: var(--cnpr_white);
  font-size: 1.1rem;
  margin: 0 auto;
  font-weight: normal;
  margin-bottom: 1rem;
}